


























import RepositoryOverview from "@/components/repositories/RepositoryOverview.vue";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import { routerModule, repositoriesModule, jobsModule } from "@/store/modules";
import JobsList from "@/components/jobs/JobsList.vue";
import SpeedDial, { ISpeedDialItem } from "@/components/misc/SpeedDial.vue";

@Component({
  components: {
    RepositoryOverview,
    JobsList,
    SpeedDial
  }
})
export default class RepositoryView extends Mixins(LoadingScreen) {
  get speedDialItems(): ISpeedDialItem[] {
    return [
      {
        to: `/repositories/${this.repoId}/logs`,
        icon: "mdi-file-document-outline",
        name: this.$t("repositories.show-logs").toString()
      },
      {
        to: `/repositories/${this.repoId}/snapshots`,
        icon: "mdi-folder-image",
        name: this.$t("repositories.show-snapshots").toString()
      },
      {
        to: `/repositories/${this.repoId}/edit`,
        icon: "mdi-pencil",
        name: this.$t("repositories.edit-repository").toString()
      },
      {
        click: this.deleteRepository,
        icon: "mdi-delete",
        name: this.$t("repositories.delete-repository").toString()
      }
    ];
  }

  mounted() {
    this.setLoading(true);
    setTimeout(() => this.setLoading(false), 500);
  }

  get repoId(): string {
    return routerModule.params.repositoryId;
  }

  async deleteRepository() {
    await repositoriesModule.deleteRepository(Number(this.repoId));
    this.$router.replace("/repositories");
  }

  get relatedJobs() {
    return jobsModule.getByRepository(this.repoId);
  }
}
